.services-container
{
    min-height: 100vh;
    background-color: #AFE1E1;
}

.container{
    display: flex;
    flex-direction: row;
}

.left-container
{
    width: 50%;
    margin-top: 50px;
    margin-left: 60px;
}

h1
{
    margin-top: 5px;
    font-family: 'Salsa', cursive;
    font-size: 60px;
    letter-spacing: 3px;
    font-weight: 800;
}
p
{
    margin-top: 30px;
    font-family: 'Amaranth', sans-serif;
    font-size: 25px;
    color: dimgray;
    margin-bottom: 60px;
    font-weight: 600;
}
.custom-hr {
    border: none; /* Remove the default border */
    border-top: 3px solid #000; /* Set the thickness and color of the line */
    margin-top: 5px; /* Adjust the vertical spacing if needed */
  }
  h2
  {
      margin-top: 70px;
      margin-left: 200px;
      font-family: 'Amaranth', sans-serif;
      font-size: 40px;
      line-height: 80px;
  }  
  @media screen and (max-width: 1024px) {
    .services-title{
        font-size: 50px;
        text-align: center;
    }
    .left-container{
        margin-left: auto;
    }
    .services-text{
        font-size: 25px;
        padding: 20px;
        text-align: left;
    }
    .h2{
        padding: 50px;
        margin-left: auto;
        margin-top: 5px;
        font-size: 40px;
    }
  }

  @media screen and (max-width: 600px)
  {
    .services-title{
        font-size: 40px;
        text-align: center;
    }
    .left-container{
        margin-left: auto;
    }
    .services-text{
        font-size: 25px;
        padding: 20px;
        text-align: left;
    }
    .h2{
        padding: 50px;
        margin-left: auto;
        margin-top: 5px;
        font-size: 35px;
    }
  }